import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import './movies.css';
import PropTypes from 'prop-types';
import LinesEllipsis from 'react-lines-ellipsis';

function Movie({title,poster,descrption}){
  return(
      <div className="Movie float width_50 height_100px ">
        <div className="float width_50 text_align_center"><MoviePoster poster={poster}/></div>
        <div className="float width_50">
          <div className="inline_block Movie_title"><h1>{title}</h1></div>
          <div className="inline_block Movie_descrption"><h1>
          <LinesEllipsis
            text={descrption}
            maxLine='3'
            ellipsis='...'
            trimRight
            basedOn='letters'
            />
          </h1></div>
        </div>
      </div>
  );
}

Movie.propTypes ={
  title:PropTypes.string.isRequired,
  poster:PropTypes.string.isRequired
}

function MoviePoster({poster}){
  return(
    <img src={poster} alt="Movie Poster"/>
  );
}

MoviePoster.propTypes ={
  poster:PropTypes.string.isRequired
}

export default Movie;



